import React from "react";
import style from "./Task.module.css";

function TaskLists({ editTaskHandler, ModalHandler, task }) {
  const [newTask, newNote, newTaskId] = [task.task, task.note, task.taskId];

  const editHandler = () => {
    editTaskHandler(newTaskId);
    ModalHandler();
  };

  return (
    <>
      {task && (
        <li className={style.li}>
          <span>{newTask}</span>
          <div
            className={style.dropdown}
            style={{
              bottom: 0,
              backgroundColor: "rgba(0,0,0,0.25)",
              marginRight: "4px",
            }}
          >
            <span onClick={editHandler}>...</span>
          </div>
        </li>
      )}
    </>
  );
}

export default TaskLists;
